*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App{
  padding: 1rem 3rem;
}
.App h1{
  color: blue;
  margin-bottom: 1rem;
  font-weight: bold;
}
.gallery{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  row-gap: 3rem;
}

.photo{
  width: 250px;
  height: 300px;
  overflow: hidden;
}

.photo img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 1s;
}

.photo img:hover{
  transform: scale(1.2);
}
